/* Base */
*,
*::before,
*::after {
  box-sizing: inherit;
}

a:empty,
ul:empty,
dl:empty,
div:empty,
section:empty,
article:empty,
p:empty,
h1:empty,
h2:empty,
h3:empty,
h4:empty,
h5:empty,
h6:empty {
  display: none;
}

html {
  font-size: var(--body-fs);
  box-sizing: border-box;
  height: 100%;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-size: 1.5rem;
  letter-spacing: 0.07rem;
  line-height: calc(1 + 0.8 / var(--body-fs));
  max-width: var(--container-xl);
  margin: 0 auto;
  padding: 0 2rem;
  flex: 1 0 auto;
  font-family: 'Jost', sans-serif;
  font-style: normal;
  font-weight: var(--body-fw);
}

@media only screen and (min-width: 750px) {
  body {
    font-size: 1.6rem;
    line-height: calc(1 + 0.8 / var(--body-fs));
    padding: 0 5rem;
  }
}

::selection {
  background-color: var(--background-color);
}

h1,
.h1,
h2,
.h2 {
  font-family: var(--heading-font);
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.03em;
  line-height: 1.2em;
}

h1,
.h1 {
  font-size: var(--heading-1-fs);
}

@media only screen and (min-width: 750px) {
  h1,
  .h1 {
    font-size: var(--heading-1-fs);
  }
}

h2,
.h2 {
  font-size: var(--heading-2-fs);
}

@media only screen and (min-width: 750px) {
  h2,
  .h2 {
    font-size: var(--heading-2-fs);
  }
}

.skip-to-content-link:focus {
  z-index: 9999;
  position: inherit;
  overflow: auto;
  width: auto;
  height: auto;
  clip: auto;
}

.link {
  cursor: pointer;
  display: inline-block;
  border: none;
  box-shadow: none;
  background-color: transparent;
  padding: 0.4rem;
  font-size: 1.6rem;
  line-height: 1;
  text-decoration: underline;
  color: #000;
}

.link:hover {
  color: #000;
}

.button, .btn {
  font-family: inherit;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: none;
  padding: 1.5rem 3rem;
  text-decoration: none;
  background-color: #000;
  color: #fff;
  min-width: 12rem;
  height: auto;
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
  line-height: calc(1 + 0.2 / var(--body-fs));
}

.button::selection {
  background-color: #000;
}

.button--secondary, .btn-2 {
  color: #000;
  box-shadow: 0 0 0 0.1rem #000;
  background-color: transparent;
}

.button--secondary:hover {
  box-shadow: 0 0 0 0.2rem #000;
}

.gift-card {
  padding-bottom: 3.4rem;
}

.gift-card__title {
  text-align: center;
  word-break: break-word;
  padding: 4rem 0 1.7rem;
}

@media only screen and (min-width: 990px) {
  .gift-card__title {
    padding: 6rem 0 2.6rem;
  }
}

.gift-card__image-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-color: var(--background-color);
  margin-bottom: 0.8rem;
  margin: 0 auto;
}

@media only screen and (min-width: 750px) {
  .gift-card__image-wrapper {
    margin-bottom: 0;
    width: 40rem;
  }
}

.gift-card__image {
  max-width: 100%;
  padding: 0 2rem;
  height: auto;
}

@media only screen and (min-width: 750px) {
  .gift-card__image {
    padding: 0;
  }
}

.gift-card__heading {
  font-weight: 400;
  margin: 2.5rem 0 1rem;
}

.gift-card__price {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.6rem;
  font-weight: 400;
  letter-spacing: 1px;
  line-height: calc(1 + 0.5 / var(--body-fs));
  opacity: 0.85;
}

.gift-card__price > p:first-child {
  margin-bottom: 1rem;
}

@media only screen and (min-width: 750px) {
  .gift-card__price {
    font-size: 2rem;
  }
}

.gift-card__label:not(.badge) {
  font-weight: 400;
  opacity: 0.7;
}

.gift-card__number {
  background-color: transparent;
  border: none;
  color: var(--text-color);
  font-size: 1.8rem;
  font-weight: 400;
  line-height: calc(1 + 0.6 / var(--body-fs));
  text-align: center;
  width: 100%;
  margin-bottom: 1rem;
  padding: 5px 5px 4px;
}
.gift-card__number:focus {
  box-shadow: 0 0 0 0.05em #4a4a4a;
}

@media only screen and (min-width: 750px) {
  .gift-card__number {
    font-size: 1.8rem;
  }
}

.gift-card__text {
  margin-bottom: 4rem;
  opacity: 0.7;
}

.gift-card__information {
  text-align: center;
  margin-top: 3rem;
}

.gift-card__label {
  font-size: 1.3rem;
  letter-spacing: 0.05rem;
  font-weight: 500;
  display: inline;
  margin-left: 1rem;
}

.badge {
  border: 1px solid transparent;
  margin: 1.7rem 0 1rem 1rem;
  border-radius: 4rem;
  display: inline-block;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
  line-height: 1;
  padding: 0.6rem 1.3rem;
  text-align: center;
  background-color: var(--background-color);
  border-color: rgba(var(--subtext-color), 0.04);
  color: var(--text-color);
}

.badge--inverse {
  background-color: var(--text-color);
  border-color: var(--text-color);
  color: var(--background-color);
}

.caption-large {
  font-size: 1.3rem;
  line-height: calc(1 + 0.5 / var(--body-fs));
  letter-spacing: 0.04rem;
}

.gift-card__copy-code {
  margin-bottom: 2.2rem;
}

.gift-card__qr-code {
  margin-top: 3rem;
}

@media only screen and (min-width: 750px) {
  .gift-card__qr-code {
    margin-top: 5rem;
  }
}

.gift-card__qr-code img {
  margin: 0 auto;
}

.gift_card__apple-wallet {
  line-height: 0;
  display: block;
  margin-bottom: 5rem;
}

.gift-card__buttons {
  display: flex;
  flex-direction: column;
  max-width: 25rem;
  flex-wrap: wrap;
  margin: 0 auto;
}

.gift-card__buttons > .button:first-child {
  display: block;
  margin-bottom: 2rem;
}

/*
  Focus ring - default (with offset)
*/

/* Fallback - for browsers that don't support :focus-visible, a fallback is set for :focus */
*:focus {
  outline: 0;
  box-shadow: none;
}

*:focus-visible {
  outline: 0.2rem solid rgba(var(--subtext-color), 0.5);
  outline-offset: 0.3rem;
  box-shadow: 0 0 0 0.2rem #000;
}

/* Fallback - for browsers that don't support :focus-visible, a fallback is set for :focus */
.focused, .no-js *:focus {
  outline: 0.2rem solid rgba(var(--color-foreground), 0.5);
  outline-offset: 0.3rem;
  box-shadow: 0 0 0 0.2rem #000;
}

.button:focus-visible {
  box-shadow: 0 0 0 0.2rem #000;
}

/* Negate the fallback side-effect for browsers that support :focus-visible */
.no-js *:focus:not(:focus-visible) {
  outline: 0;
  box-shadow: none;
}

.button:focus {
  box-shadow: 0 0 0 0.2rem #000;
}

.button--secondary:focus-visible {
  box-shadow: 0 0 0 0.2rem #000;
}

.button--secondary:focus {
  box-shadow: 0 0 0 0.2rem #000;
}

.form__message {
  align-items: center;
  display: flex;
  font-size: 1.2rem;
  line-height: 1;
  margin-top: 1rem;
  justify-content: center;
}

.form__message .icon {
  flex-shrink: 0;
  height: 1.3rem;
  margin-right: 0.5rem;
  width: 1.3rem;
}

@media print {
  .no-print {
    display: none;
  }
}

.gradient {
  background: #fff;
  background-attachment: fixed;
}
